<template>
  <div id="app">
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" id="header-menu">
      <el-menu-item index="/">JEDDAK</el-menu-item>
      <el-menu-item index="/task">任务管理</el-menu-item>
      <el-menu-item index="/model">模型管理</el-menu-item>
      <el-menu-item index="/data">数据管理</el-menu-item>
      <el-menu-item v-if="this.$global.isAdmin" index="/admin">用户管理</el-menu-item>
      <el-menu-item index="/about">使用指南</el-menu-item>
    </el-menu>
    <router-view/>
  </div>
</template>

<script>
// @ is an alias to /src
export default {
  name: 'App',
  data: function() {
    return {
      activeIndex: null,
    }
  },
  created() {
    this.checkLocalToken();
    if (window.localStorage.getItem("jeddak_isAdmin") ) {
      this.$global.isAdmin = JSON.parse(window.localStorage.getItem("jeddak_isAdmin"));
    }

    if (window.localStorage.getItem("jeddak_partyNumber") ) {
      this.$global.partyNumber = JSON.parse(window.localStorage.getItem("jeddak_partyNumber"));
    }

    if (window.localStorage.getItem("jeddak_taskChain") ) {
      this.$global.taskChainForm = JSON.parse(window.localStorage.getItem("jeddak_taskChain"));
    }

    //在页面刷新时将vuex里的信息保存到localStorage里
    window.addEventListener("beforeunload",()=>{
      window.localStorage.setItem("jeddak_isAdmin",JSON.stringify(this.$global.isAdmin));
      window.localStorage.setItem("jeddak_partyNumber",JSON.stringify(this.$global.partyNumber));
      window.localStorage.setItem("jeddak_taskChain",JSON.stringify(this.$global.taskChainForm));
    })
  },
  updated() { this.checkLocalToken() },
  methods: {
    checkLocalToken: function () {
      this.activeIndex = this.$route.path;
      const tok = window.localStorage.getItem('jeddak_token');
      if (this.$route.path === "/login") {
        if (tok !== null) {
          this.$router.push("/");
        }
      }
      else if (tok === null) {
        this.$router.push("/login");
      }
    },
    handleSelect: function (key, keyPath) {
      if (this.$route.path !== keyPath[0]) {
        this.$router.push(keyPath[0]);
      }
    }
  },
}
</script>

<style>
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
  }
  #header-menu {
    margin-bottom: 20px;
  }
  #top-buttons {
    margin-bottom: 10px;
  }

  .finish {
    color: green;
  }
  .error {
    color: red;
  }
  .run, .ready {
    color: mediumpurple;
  }
</style>
