<script>
let isAdmin=false;
export default {
  name: "Global",
  isAdmin,
  partyNumber: null,
  //全局的模块配置表单
  moduleTable: [],
  //要修改的是table里模块的index
  indexOfModuleTable: 0,
  //是否是修改模块，false是添加模块，true为修改模块
  isEdit: false,
  taskChainForm:[],
  //模块dialog在增加时按钮显示为增加，在修改时按钮显示为修改
  moduleDialogButtonLabel(isEdit){
    if(isEdit){
      return "修改"
    }else {
      return "添加"
    }
  },
  //检测是否为正整数
  checkPositiveInteger(item){
    if(item === null){
      return false;
    }
    return /^[1-9]\d*$/.test(item);
  },
  //检测数字是否属于[0,1]，larger&equal
  checkE02E1(item){
    if(this.checkIsValue(item)){
      let fi = parseFloat(item);
      if(fi>=0 && fi<=1){return true;}
    }
    return false;
  },
  //检测数字是否属于(0,1]，larger&equal
  checkL02E1(item){
    if(this.checkIsValue(item)){
      let fi = parseFloat(item);
      if(fi>0 && fi<=1){return true;}
    }
    return false;
  },
  //检测数字是否属于(0,1)，larger&small
  checkL02S1(item){
    if(this.checkIsValue(item)){
      let fi = parseFloat(item);
      if(fi>0 && fi<1){return true;}
    }
    return false;
  },
  //检测数字是否大于等于0
  checkGE0(item){
    if(this.checkIsValue(item)){
      let fi = parseFloat(item);
      if(fi>=0){return true;}
    }
    return false;
  },
  //检测数字是否大于等于2
  checkGE2(item){
    if(this.checkIsValue(item)){
      let fi = parseFloat(item);
      if(fi>=2){return true;}
    }
    return false;
  },
  //检测数字是否大于0
  checkGL0(item){
    if(this.checkIsValue(item)){
      let fi = parseFloat(item);
      if(fi>0){return true;}
    }
    return false;
  },
  //检测是否为数值
  checkIsValue(item){
    if(item === null){
      return false;
    }
    return /^\d+(\.\d+)?$/.test(item);
  },
  //根据参与方个数动态初始化参与方需要动态变化的参数
  formatPartyPara(party, config, label, num, labOpt, key){
    let ans = [];
    party = [];
    config = [];
    label = [];
    for(let i=0;i<num;i++){
      party.push(null);
      let p = {'name':key + i};
      config.push(p);
      let l = '第' + (i+1) + '个参与方' + labOpt
      label.push(l);
    }
    ans.push(party);
    ans.push(config);
    ans.push(label);
    return ans
  },
  //根据参与方个数动态初始化参与方需要动态变化的参数，且此参数会被其他参数影响，需要控制是否show
  //isShow是用来控制是否展示，isFirstTime用来判断参数是否符合要求时使用，如果第一次被触发，则不需要变红，以后参数不正确input框会变红
  formatPartyParaControlShow(party, config, label, num, labOpt, key){
    let ans = [];
    party = [];
    config = [];
    label = [];
    for(let i=0;i<num;i++){
      party.push(null);
      let p = {'name':key + i, 'isShow': false, 'isFirstTime': true,};
      config.push(p);
      let l = '第' + (i+1) + '个参与方' + labOpt
      label.push(l);
    }
    ans.push(party)
    ans.push(config)
    ans.push(label)
    return ans
  },
  //只初始化列表
  formatList(obj, num){
    obj = [];
    for(let i = 0; i<num; i++){
      obj.push(null)
    }
    return obj
  },
  //复制Json
  copyJson(obj){
    return JSON.parse(JSON.stringify(obj))
  },
  //把内容写localstorage
  writeLocalStorage(itemName, item){
    window.localStorage.setItem(itemName, JSON.stringify(item));
  },
  //从localstorage读取内容
  readLocalStorage(itemName){
    if(window.localStorage.getItem(itemName)) {
      return JSON.parse(window.localStorage.getItem(itemName))
    }
    return null
  },
  //从input中解析出array
  parseInput2IntArray(input){
    if(input === null){
      return null;
    }
    input = input + '';
    let tc1 = input.split(',');
    let tc2 = input.split('，');
    if(tc1.length<tc2.length){
      tc1 = tc2;
    }
    for(let j = 0;j<tc1.length;j++){
      tc1[j] = parseInt(tc1[j]);
    }
    return tc1
  },
  //把str数组解析成数值数组，如果str不能被转成数值则str保持原来的样子
  parseStrArray2IntArray(input){
    if(input===null){
      return input
    }
    if(typeof(input)=='string'){
      let keyInt = parseInt(input);
      let keyFLoat = parseFloat(input);
      let keyNum = keyInt;
      if(keyInt !== keyFLoat){
        keyNum = keyFLoat;
      }
      if(!isNaN(keyNum)){
        input = keyNum;
      }
      return input
    }
    for(let i=0;i<input.length;i++){
      if(input[i] !== null){
        let keyInt = parseInt(input[i]);
        let keyFLoat = parseFloat(input[i]);
        let keyNum = keyInt;
        if(keyInt !== keyFLoat){
          keyNum = keyFLoat;
        }
        if(!isNaN(keyNum)){
          input[i] = keyNum;
        }
      }
    }
    return input
  },
  //把数组转成str，用于反向解析成用户操作时
  array2str(input){
    if(input === null){
      return null
    }
    let out = input[0];
    for(let i=1;i<input.length;i++){
      out = out + ',' + input[i];
    }
    return out
  },
  //把数组转成str，用于反向解析成用户操作时，增加了控制分割符号的方式
  array2strWithSeg(input, seg){
    if(input === null){
      return null
    }
    let out = input[0] + '';
    for(let i=1;i<input.length;i++){
      out = out + seg + input[i];
    }
    return out
  },
  //用于汇总参数时处理需要跟随其他参数而动态变更的参数
  handleIsShowPara(item, item_config, type){
    let ans = []
    for(let i=0;i<item.length;i++){
      if(item_config[i].isShow){
        if(item[i] === null || item[i] === ''){
          ans.push(null);
          continue;
        }
        if(type === 'int'){
          ans.push(parseInt(item[i]));
        }else if(type==='float'){
          ans.push(parseFloat(item[i]));
        }else {
          ans.push(item[i]);
        }
      }else {
        ans.push(null);
      }
    }
    return ans
  },
}
</script>