<template>
    <div class="home">
        <el-row id="top-buttons">
            <el-button type="primary" v-on:click='changePasswordDialogVisible=true'>
                修改密码
                <i class="el-icon-key"></i>
            </el-button>
            <el-button v-on:click="logout">
                注销登录
                <i class="el-icon-switch-button"></i>
            </el-button>
        </el-row>
        <el-row :gutter="20">
            <el-col :span="24" :xs="24" :sm="12">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <b>账号信息</b>
                    </div>
                    <div>参与方名称：{{ party_info.party_name || "未声明" }}</div>
                    <hr/>
                    <div>通信组件类型：{{ party_info.syncer_type || "未声明" }}</div>
                    <hr/>
                    <div>通信组件状态：{{ party_info.syncer_status || "未连接" }}</div>
                </el-card>
            </el-col>
            <el-col :span="24" :xs="24" :sm="12">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <b>任务信息</b>
                    </div>
                    <div>成功任务数：{{ task_info.finish || 0 }}</div>
                    <hr/>
                    <div>失败任务数：{{ task_info.error || 0 }}</div>
                    <hr/>
                    <div>未完成任务数：{{ task_info.ready || 0 + task_info.run || 0 }}</div>
                </el-card>
            </el-col>
        </el-row>
        <!-- change password dialog -->
        <el-dialog
            title="修改密码"
            :visible="changePasswordDialogVisible"
            :before-close="changePasswordDialogClose"
            width="30%">
          <el-form ref="form" :model="form" :rules="rules">
            <el-form-item prop="password">
              <el-input
                  placeholder="请输入原密码"
                  v-model="form.password"
                  v-on:keyup.enter.native="onSubmit"
                  show-password >
              </el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input
                  placeholder="请输入新密码"
                  v-model="form.new_password"
                  v-on:keyup.enter.native="onSubmit"
                  show-password >
              </el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="changePassword">修改</el-button>
          <el-button type="info" @click="changePasswordDialogClose">取消</el-button>
        </span>
        </el-dialog>
    </div>
</template>

<script>
    import {sha256} from "js-sha256";

    export default {
        name: 'Home',
        data: function () {
            return {
                //修改密码表单
                form: {
                  username:"",
                  password:"",
                  new_password:""
                },
                password:'',
                new_password: '',
                salt:'',
                new_salt:'',
                rules: {
                  username: [
                    { required: true, trigger: 'blur' }
                  ],
                  password: [
                    { required: true, trigger: 'blur' }
                  ],
                  new_password: [
                    { required: true, trigger: 'blur' }
                  ],
                },
                //修改密码的对话框是否可见
                changePasswordDialogVisible:false,
                // 数据：账号信息
                party_info: {
                    party_name: "",
                    syncer_type: "",
                    syncer_status: "",
                },
                // 数据：任务信息
                task_info: {
                    task_num: 0,
                    finished_num: 0,
                    unfinished_num: 0,
                },
                // 数据：参与方初始化信息
                party_init_form: {
                    party_name: "",
                    syncer_server: "",
                    syncer_type: "",
                },
                // 数据：任务清空信息
                clear_topic_form: {
                    syncer_server: "",
                    syncer_type: "",
                },
                // 标识：是否展示"加载中"动画
                loading: false,
            }
        },
        mounted() {
            if (window.localStorage.getItem('jeddak_token') !== null) {
                this.fetch_party_info();
                this.fetch_task_info();
            }
            if (window.localStorage.getItem('jeddak_username') !== null) {
              this.form.username = window.localStorage.getItem('jeddak_username');
            }
        },
        methods: {
            // 获取参与方信息
            fetch_party_info() {
                this.$axios.get("/board/party",{
                    headers: {
                        'auth': window.localStorage.getItem('jeddak_token')
                    }
                }).then(resp => {
                    this.party_info.party_name = resp.data.data.party_name;
                    this.party_info.syncer_type = resp.data.data.syncer_type;
                    if (resp.data.data.party_name !== "") {
                        this.party_info.syncer_status = "已连接";
                    }
                }).catch(err => {
                    console.log(err);
                    this.$notify.error({
                        title: '提示',
                        message: '操作失败，可能不具备权限，请尝试注销重新登录或联系管理员'
                    });
                });
            },
            // 获取任务信息
            fetch_task_info() {
                let payload = new URLSearchParams({
                    filter: JSON.stringify({
                        'deleted': false,
                    }),
                    req_type: 'stat'
                })
                this.$axios.get("/board/tasks?"+payload.toString(), {
                    headers: {
                        'auth': window.localStorage.getItem('jeddak_token')
                    }
                }).then(resp => {
                    if (resp.data.data === undefined) {
                        return
                    }
                    this.task_info = resp.data.data;
                }).catch(err => {
                    console.log(err);
                    this.$notify.error({
                        title: '提示',
                        message: '操作失败，可能不具备权限，请尝试注销重新登录或联系管理员'
                    });
                });
            },
            // 登出
            logout() {
                window.localStorage.removeItem('jeddak_token');
                window.localStorage.removeItem('jeddak_isAdmin');
                this.$global.isAdmin=false;
                this.$router.push("/login");
            },
            //修改密码
            async changePassword(){
              // get salt
              try{
                const { data: resp } = await this.$axios.post("/board/user", {
                  'filter': {name: this.form.username},
                  'req_type': 'get_change_password_salt'
                });
                if(resp.data.salt !== ""){
                  this.salt = resp.data.salt
                  this.new_salt = resp.data.new_salt
                }
              }catch (error) {
                if (error.response) {
                  console.log(error.response.data);
                  console.log(error.response.status);
                  console.log(error.response.headers);
                  this.$notify.error({
                    title: '提示',
                    message: '获取盐值失败'
                  });
                  this.loading = false;
                }
                console.log(error);
              }
              this.password = sha256(this.salt + this.form.password).toString();
              this.new_password = sha256(this.new_salt + this.form.new_password).toString();
              // send change password request
              this.$axios.patch("/board/user", {
                  req_type: 'update_password',
                  filter: {
                      'name': this.form.username,
                      'password': this.password,
                  },
                  field: {
                      'password': this.new_password,
                      'salt': this.new_salt
                  }
              }, {
                headers: {
                  'auth': window.localStorage.getItem('jeddak_token')
                }
              })
              .then(resp => {
                console.log(resp);
                this.loading = false;
                window.localStorage.setItem("jeddak_token", resp.data.data.auth);
                this.$notify.success({
                  title: '提示',
                  message: '修改成功'
                });
              }).catch(err => {
                console.log(err);
                this.$notify.error({
                  title: '提示',
                  message: '修改失败'
                });
                this.loading = false;
              });
              this.changePasswordDialogVisible=false;
              this.form.password = "";
              this.form.new_password="";
            },
          changePasswordDialogClose(){
              this.form.password = "";
              this.form.new_password="";
              this.changePasswordDialogVisible=false;
            }
        }
    }
</script>
<style scoped>
    .box-card {
        margin-bottom: 10px;
    }
    hr {
        background-color: lightgray;
        border: 0;
        height: 1px;
        margin: 15px 0;
    }
</style>
